<template>
    <b-row>
    <b-col cols="12" class="header">
      <h4>
        Пользователь {{ key }}
      </h4>
      <b-jumbotron>
        <template slot="lead">
          ФИО: {{ user.name }}<br>
          Адрес: {{user.adress}}<br>
          Дата рождения: {{user.birthday}}<br>
          Серия Номер: {{user.passport.seriya}} {{user.passport.number}}<br>
          
        </template>
        <hr class="my-4">
        <b-btn class="edit-btn" variant="success" @click.stop="editboard(key)">Изменить данные</b-btn>
        <b-btn variant="danger" class="delete" @click.stop="deleteboard(key)">Удалить пользователя</b-btn>
      </b-jumbotron>
    </b-col>
  </b-row>
</template>
<script>
import firebase from '../Firebase'

export default {
    name: "UserComponent",
    data: () => ({
     key: '',
     user: {}
    }),
    mounted () {
    const ref = firebase.firestore().collection('users').doc(this.$route.params.id);
    ref.get().then((doc) => {
      if (doc.exists) {
        this.key = doc.id;
        this.user = doc.data();
      } else {
        alert("No such document!");
      }
    });
  },
  methods: {
      editboard (id) {
        this.$router.push({
         name: 'EditBoard',
         params: { id: id }
        })
    },
       deleteboard (id) {
      firebase.firestore().collection('users').doc(id).delete().then(() => {
        this.$router.push({
          name: 'baselayout'
        })
      }).catch((error) => {
        alert("Error removing document: ", error);
      });
    }
  }
  }

</script>

<style lang="scss">
    .header{
        margin-top: 40px;
    }
    .delete{
        margin-left: 20px;
    }
</style>